// logo
.logo {
    width: 96px;
    height: 36px;
    background-image: url("../images/logo.png");
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
    a {
        width: 100%;
        height: 100%;
        display: block;
        text-indent: 101%;
        overflow: hidden;
        white-space: nowrap;
    }
}